.form__input label.is-invalid input {
  outline: 2px solid #ff1553;
}
.form__input label.is-invalid input:focus, .form__input label.is-invalid input:hover {
  outline: 2px solid rgba(255, 21, 83, 0.5);
}
.form__input label .input-message {
  position: absolute;
  top: -32px;
  left: 0;
  font-size: 15px;
  line-height: 15px;
}
.form__input label .input-message.is-invalid {
  color: #ff1553;
}
.form__input label .input-message.is-valid {
  color: #2c39f2;
}
@media (max-width: 1023px) {
  .form__input label .input-message {
    top: -19px;
  }
}

.form__button {
  display: block;
  width: 100%;
  font-size: 14px;
  line-height: 21px;
  font-weight: 600;
  padding: 11px;
  text-align: center;
  background-color: #f9fbfd;
}

.form__checkbox {
  position: relative;
  display: flex;
  order: 1;
  padding-top: 2px;
}
@media (max-width: 767px) {
  .form__checkbox {
    transform: translateY(-5px);
  }
}
.form__checkbox:hover {
  opacity: 0.7;
}
.form__checkbox:focus-within {
  outline: 1px solid #ffffff;
  outline-offset: 2px;
}
.form__checkbox label {
  position: relative;
  display: flex;
  margin-bottom: 33px;
  align-items: center;
  cursor: pointer;
}
@media (max-width: 1023px) {
  .form__checkbox label {
    margin-bottom: 33px;
  }
}
@media (max-width: 767px) {
  .form__checkbox label {
    margin-bottom: 22px;
  }
}

.form__mark {
  position: absolute;
  left: 0;
  width: 18px;
  height: 18px;
  border: 1px solid #ffffff;
  background-color: #f9fbfd;
}
@media (max-width: 767px) {
  .form__mark {
    width: 20px;
    height: 20px;
  }
}

[type=checkbox]:checked + .form__mark {
  background-image: url("../img/svg/check.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.form__label {
  font-size: 10px;
  line-height: 13px;
  font-weight: 400;
  color: #f9fbfd;
  margin-left: 23px;
}
@media (max-width: 767px) {
  .form__label {
    margin-left: 28px;
  }
}

